<template>
  <div class="card">
    <div class="card-header">
      MountHook
    </div>

    <div class="card-body">

    </div>
  </div>
</template>

<script setup>
import { onBeforeMount, onMounted, onBeforeUnmount, onUnmounted } from "vue";

// 콜백 함수로 정의
onBeforeMount(() => {
  console.log("onBeforeMount 콜백");
});

onMounted(() => {
  console.log("onMounted 콜백");
});

onBeforeUnmount(() => {
  console.log("onBeforeUnmount 콜백");
});

onUnmounted(() => {
  console.log("onUnmounted 콜백");
});
</script>

